.wrapper {
    position: relative;
    padding: 50px 30px;

    &.frontpageHero {
        padding: 170px 30px 70px;
    }

    @media only screen and (min-width: 768px) {
        padding: 100px 0;

        &.frontpageHero {
            padding: 255px 0 140px;
            min-height: 70vh;
        }

        &.footerBar {
            padding: 100px 30px;
        }
    }
}

.newsletterSignup {
    max-width: 600px;
    margin: 0 auto;
}

.ctaGrid{
    align-items:center;
    margin-top:21px;
}

.resourceImage{
    max-width:88%;
    margin: 0 auto;
}

.resourceTitle{
    max-width:80%;
    margin: 0 auto;
}

@import "../components/_timeline";

@import "../components/_modules";
